@font-face {
  font-family: "Gotham";
  src: url("../fonts/GothamPro-Bold.woff2") format("woff2"), url("../fonts/GothamPro-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  height: 100%;
  box-sizing: border-box; }

body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #33312C;
  background-color: #C1C0CD; }

header {
  flex-grow: 1; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.hidden {
  display: none !important; }

.container {
  margin: 0 auto;
  width: 1209px;
  max-width: 100%;
  padding: 0 20px; }

.h2 {
  margin: 0;
  margin-left: 5px;
  font-family: "Gotham", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.25;
  letter-spacing: 0em;
  text-transform: uppercase; }

@media (max-width: 550px) {
  .h1 {
    font-size: 33px; } }

.top {
  margin-bottom: 85px; }

.top__caption {
  margin-bottom: 22px; }

.top__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.top__item {
  margin-bottom: 13px; }

.top__button {
  margin: 21px auto 0;
  padding: 14px 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Gotham", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0em;
  text-transform: uppercase;
  color: #191925;
  border: 1px solid #DADADA;
  border-radius: 5px;
  background-color: transparent;
  transform: skewX(-17deg); }
  .top__button:hover, .top__button:focus, .top__button:active {
    cursor: pointer;
    text-decoration: underline;
    opacity: 0.6; }
  .top__button span {
    transform: skewX(17deg); }

.min-card {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 7px 29px 7px 37px;
  background-color: #ffffff;
  border-radius: 5px;
  border: 1px solid transparent; }
  .min-card:focus, .min-card:hover, .min-card:active {
    border: 1px solid #5A3EC4; }

.min-card__column {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between; }
  .min-card__column:first-child {
    flex-grow: 0.46; }
  .min-card__column:last-child {
    flex-grow: 0.1; }

.min-card__number {
  margin-right: 5px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.6;
  letter-spacing: 0px; }

.min-card__img {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 107px;
  height: 100%;
  margin-right: 5px; }
  .min-card__img img {
    object-fit: cover;
    width: 100%;
    height: 100%; }

.min-card__caption {
  margin: 0;
  margin-right: 5px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.6;
  letter-spacing: 0px; }

.min-card__star {
  margin-left: 5px; }
  .min-card__star svg {
    margin-right: 2px; }

.min-card__cash {
  margin-right: 5px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.6;
  letter-spacing: 0px; }
  .min-card__cash svg {
    margin-right: 14px; }

.min-card__link {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 5px;
  padding: 9px 18px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.1;
  letter-spacing: 0em;
  color: #191925;
  border: 1px solid #DADADA;
  border-radius: 5px; }
  .min-card__link:hover, .min-card__link:focus, .min-card__link:active {
    cursor: pointer;
    text-decoration: underline;
    opacity: 0.6; }
  .min-card__link:first-child {
    border-color: #67BC6D; }
  .min-card__link:last-child {
    color: #ffffff;
    border-color: #E2455A;
    background-color: #E2455A; }

@media (max-width: 900px) {
  .min-card {
    display: block; } }

@media (max-width: 550px) {
  .min-card {
    padding: 7px 15px; }
  .min-card__column:last-child {
    display: block; }
  .min-card__link {
    margin-bottom: 20px; } }
